import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { ImageManagerComponent }   from '../view/image-manager.component';
import { DataManagerComponent }   from '../view/data-manager.component';

const routes: Routes = [
    { path: '', redirectTo: '/imageManager', pathMatch: 'full' },
    { path: 'imageManager', component: ImageManagerComponent },
    { path: 'dataManager', component: DataManagerComponent }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }


