import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { AppRoutingModule } from './app-routing.module';

import { SidebarMenuComponent } from '../plugin/sidebar-menu.component';
import { NavbarMenuComponent } from '../plugin/navbar-menu.component';

export const PLUGIN_MODULES = [
    SidebarMenuComponent, NavbarMenuComponent
];

@NgModule({
    imports: [BrowserModule, AppRoutingModule, NgbModule.forRoot()],
    exports: PLUGIN_MODULES,
    declarations: PLUGIN_MODULES
})
export class PluginModule {
}


